import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/babitas_.jpg'
import thumb02 from '../assets/images/thumbs_ML/babitas_2.jpg'
import thumb03 from '../assets/images/thumbs_ML/babitas_3.jpg'
import thumb04 from '../assets/images/thumbs_ML/babitas_4.jpg'


import full01 from '../assets/images/fulls/babitas_.jpg'
import full02 from '../assets/images/fulls/babitas_2.jpg'
import full03 from '../assets/images/fulls/babitas_3.jpg'
import full04 from '../assets/images/fulls/babitas_4.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Babitas De Toalla Para Bebés X 6 Unidades(20 X 30cm)</h2>
                            <h4>$180</h4> 
                        </header>
                        <p>DESCRIPCIÓN:<br /> 
                        Babitas de toalla para bebe x 6 unidades.<br /> 
                        Medidas: 20 x 30cm.<br /> 
                        * Ribeteadas con cinta al bies<br /> 
                        * Se pueden personalizar con cualquier imagen o foto.<br /> 
                        * Se envia por correo (MercadoEnvios)<br /> 
                        * O se retira en la zona de quilmes, de lunes a sabado de 16 a 20. Consultar por otros dias.</p>
                       <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-82ace743-bcbf-401b-8b40-47ef23315c92" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br /> 
                        <Link to="/">Volver</Link>

                        <br /><br /> 
                    </section>
                
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex



